import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode, Component, createRef, RefObject } from "react"
import './App.css';
import autosize from "autosize";


interface State {
  val: string,
  buttonDisabled: boolean,
}

class MyComponent extends StreamlitComponentBase<State> {
  textarea: RefObject<HTMLTextAreaElement> = createRef<HTMLTextAreaElement>();
  state: Readonly<State> = { buttonDisabled: true, val: ""};
    emoji = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", 
      "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""]
      //["", "","","","","","","","","","","","",""]

  checkButAvailability(val) {
    this.setState({buttonDisabled: val === ""});
  }

  handleOnChange(e) {
    this.setState({val: e.target.value,});
    autosize(this.textarea.current);
    this.checkButAvailability(e.target.value);
  };

  handleOnClick(emoji) {
    const { val } = this.state;
    let ss = this.textarea.current?.selectionStart
    let se = this.textarea.current?.selectionEnd
    const newVal = val.slice(0, ss) + emoji + val.slice(se);
    this.setState({
      val: newVal,
    }, () => {
      if (this.textarea.current) {
        this.textarea.current?.setSelectionRange(ss + emoji.length, ss + emoji.length);
        this.textarea.current?.focus();
        autosize.update(this.textarea.current);
      }
    });
    this.checkButAvailability(newVal);
  };

  sendDataToStreamlit(e) {
    const sendValue = this.state.val
    if (sendValue !== "") {
      this.setState({val:""});
      this.textarea.current!.value = "";
      Streamlit.setComponentValue(sendValue); 
    }
  }

  handleOnKeyDown(e) {
    if (e.keyCode === 13 && e.shiftKey === false) {
      e.preventDefault();
      this.sendDataToStreamlit(e);
    }
  };

  public componentDidMount(): void {
    if (this.textarea.current) {
      autosize(this.textarea.current);
    }
    Streamlit.setFrameHeight(document.body.clientWidth <= 540 ? 100 : 175);
  }

  public componentDidUpdate(): void {
    if (this.props.args["disabled"]) {
      this.setState({ buttonDisabled: true})
    } else {
      this.setState({ buttonDisabled: false})
    }
    if (this.textarea.current) {
      autosize.update(this.textarea.current);
    }
    Streamlit.setFrameHeight(document.body.clientWidth <= 540 ? 100 : 175);
  }
  
  public render = (): ReactNode => {
    function Repeat(props) {
      let items : string[] = [];
      for (let i = 0; i < props.emoji.length; i++) {
        items.push(props.children(props.emoji[i]));
      }
      return <div className="emoijContainer">{items}</div>;
    }
   
    return (
      <div className="bodyContent">
        <div className="messageBlock">
          <textarea
            placeholder=" :"
            value={this.state.val}
            rows={1}
            onChange={ (e) => this.handleOnChange(e) }
            onKeyDown={ (e) => this.handleOnKeyDown(e) }
            disabled={this.props.args["disabled"]}
            ref={this.textarea}>
          </textarea>
          <div className="sendButton" onClick={(e) => this.sendDataToStreamlit(e)}>
            <svg viewBox="0 0 24 24" 
            aria-hidden="true" 
            color="inherit" 
            focusable="false" 
            fill={this.state.buttonDisabled ? "#a3a8b8" : "#555867"} 
            className={this.state.buttonDisabled ? "sendButtonSVG eyeqlp51 st-emotion-cache-1f3w014 ex0cdmw0": "sendButtonSVG sendButtonSVG_hover eyeqlp51 st-emotion-cache-1f3w014 ex0cdmw0"}>
              <rect width="24" height="24" fill="none"></rect>
              <path d="M3 5.51v3.71c0 .46.31.86.76.97L11 12l-7.24 1.81c-.45.11-.76.51-.76.97v3.71c0 .72.73 1.2 1.39.92l15.42-6.49c.82-.34.82-1.5 0-1.84L4.39 4.58C3.73 4.31 3 4.79 3 5.51z"></path></svg>
          </div>
        </div>
        <Repeat emoji={this.emoji}>
          {(index) => <div className="emojiButton" key={index} onClick={() => this.handleOnClick(index)}>{index}</div>}
        </Repeat>
      </div>
    )
  }

}

export default withStreamlitConnection(MyComponent);